@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root{
  --white: #fff;
  --black: #000;
  --red: #B9090B;
  font-size: 16px;
}
body {
  margin: 0;
  color: #fff;
  color: var(--white);
  background: black;
  /* background: url('https://i.imgur.com/98fn9hl.jpeg'); */
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

.header{color:white;padding:15px 20px;position:fixed;top:0;left:0;width:100%;display:flex;font-size:1rem;align-items:center;background:black;font-weight:600;z-index:2}.header .logo{color:var(--red);font-size:24px;font-weight:bold;flex:1 1}.header .categoryDrop{margin-right:30px;position:relative;display:inline-block}.header .categoryDrop:hover{cursor:pointer}.header .categoryDrop:hover .dropdown-content{display:block}.header .categoryDrop .dropIcon{margin-left:5px;height:10px}.header .categoryDrop .dropdown-content{display:none;position:absolute;right:0;opacity:0.95;background-color:#f9f9f9;min-width:160px;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);border-radius:4px;overflow:hidden}.header .categoryDrop .dropdown-content .dropItem{color:black;padding:12px 16px;text-decoration:none;display:block}.header .categoryDrop .dropdown-content .dropItem:hover{background-color:lavender}.header .favs{margin-right:55px;position:relative}.header .favs:hover{cursor:pointer}.header .favs .numberOfFavs{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);right:-25px;background-color:var(--red);border-radius:4px;padding:2px 4px;font-size:12px}

.movieCard{width:217px;height:450px;padding-bottom:10px}.movieCard:hover{cursor:pointer;border-radius:5px;background-color:rgba(255,255,255,0.75)}.movieCard:hover .tags{color:black}.movieCard img{width:100%;height:85%;object-fit:cover;border-radius:5px;overflow:hidden}.movieCard .tags{height:10%;text-align:center;font-size:14px;word-break:break-all}.movieCard .tags span{display:block}.likedCard{background-color:rgba(185,9,11,0.75);border-radius:5px}

