.movieSection{padding:50px 20px 20px 20px;margin-top:50px;max-width:calc( 100vw - 40px);overflow:hidden}.movieSection h2{text-transform:capitalize}.movieSection .movieFlex .swiper-wrapper{display:inline-flex}

